/**
 * Time to read a text
 * @description 230 words is an average count 
 */
export function timeToRead (words:number) {
  return Math.round(words / 200)
}

/**
 * Time to read a text
 * Rectify to display an user-friendly result
 */
export function timeToReadMessage (words:number) {
  let time = timeToRead(words)
  return `~ ${time + 1} ${time < 1 ? "minute" : "minutes"}`
}

/**
 * @description
 * Trim until the last point is it exists
 * Else the full excerpt if it exists
 * Else an empty string
 */
export const prettifyExcerpt = (excerpt?:string):string => !excerpt ? '' : excerpt.replace(/(?!.*\.)\n+.*/gm, '') || excerpt 