import React, { FunctionComponent, useRef, useEffect } from "react"
import { css, cx } from '@emotion/css'
import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image"

/**
 * Type
 */
interface IBlogCover {
  coverImage: IGatsbyImageData,
  copyright?: string
}

interface IParallax {
  scroll: number,
  mov: number,
  elBound: DOMRect|null,
}

/**
 * Settings
 */
const parallaxStrength:number = 1.25


/**
 * Styles
 */
const coverStyle = css`
  position: relative;
  overflow: hidden;
  box-shadow: 0px -40px 40px -40px inset #00000030;
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: solid 2vw transparent;
    border-bottom: solid 2vw white;
    border-left: solid 50vw white;
    border-right: solid 50vw white;
  }
`

const coverImgStyles = css`
  position: relative;
  border-radius: 5px;
  border-top: solid 6px white;
  border-right: solid 6px white;
  border-left: solid 6px white;
  transform-origin: center;
  transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  transition: all 0s 0s;
  background-color: #ffffff30;
  opacity: 1;
  transition: opacity linear 0.4s;
  box-shadow: rgb(0 0 0 / 33%) 0px -45px 20px -20px inset;
`

/**
 * View
 */
const BlogCover:FunctionComponent<IBlogCover> = ({ coverImage, copyright }) => {
  // set a ref on the cover container to create the visual effect
  const coverEl = useRef<HTMLDivElement>(null)

  // set a ref to manage the parallax effect
  const parallax = useRef<IParallax>({
    scroll: 0,
    mov: 0,
    elBound: null,
  })

  // handle event
  const handleScroll = (event:Event) => {
    event.preventDefault()

    if ( 
      !coverImage 
      || !coverEl.current 
      || !parallax.current.elBound
      || window.scrollY > parallax.current.elBound.height
    ) return
    
    // get scroll
    const scroll = Math.max(0, Math.round(window.scrollY * parallaxStrength))

    // get scroll distance 
    const mov = Math.round(scroll - parallax.current.scroll)

    // calcul opacity
    const opacity = 1 - (1 / parallax.current.elBound.height) * scroll * parallaxStrength

    // apply conditional style
    if ( 
      Math.sign(mov) === Math.sign(parallax.current.mov) 
      || parallax.current.scroll === 0 
    ){
      coverEl.current.style.transform = `translate3d(0, ${scroll}px, 0)`
      coverEl.current.style.opacity = opacity.toString()
    }

    // apply new values
    parallax.current.scroll = scroll
    parallax.current.mov = mov
  }

  // init parallax
  useEffect(() => {
    if ( parallax.current.elBound || !coverEl.current ) return  
    
    parallax.current.elBound = coverEl.current.getBoundingClientRect()
  }, [coverEl])

  // manage parallax effect
  useEffect(() => {
    window.addEventListener("scroll", handleScroll)
    
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <div style={{ overflow: "hidden" }}>
      <div className={cx(coverStyle, "bg-primary")}>
        <div className="container-reading">
          <div className={coverImgStyles} ref={coverEl}>
            <GatsbyImage image={coverImage} title={`Source de l'image: ${copyright || 'inconnue'}`} alt='' />
          </div>
        </div>
      </div>
    </div>
  )
}


export default BlogCover

