/**
 * Format data from graphQL into a friendly flat object
 * @param {object} data from graphQL
 */
export const formatPost = ( data:INode ):IPost => ({
  title: data?.frontmatter?.title || undefined,
  date: data?.frontmatter?.date || undefined,
  category: data?.frontmatter?.category || undefined,
  cover: data?.frontmatter?.cover?.childImageSharp?.gatsbyImageData || undefined,
  coverCopy: data?.frontmatter?.coverCopy || undefined,
  excerpt: data?.excerpt || undefined,
  link: data?.fields?.slug || undefined,
  body: data?.body || undefined,
  words: data?.wordCount?.words || undefined
})

