import React, { FunctionComponent } from "react"
import { graphql } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"
import mdxComponents from "services/mdxConvertor"
import { formatPost } from "services/formaters"
import { timeToReadMessage } from "services/tools"
import Layout from "partials/layouts/layout-page"
import Comments from "partials/blog/comments"
import Cover from "partials/blog/cover"

/**
 * Types
 */
interface IBlogTemplate {
  data: IMdxData
}

/**
 * QUery
 */
export const pageQuery = graphql`
  query($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      id
      body
      wordCount {
        words
      }
      frontmatter {
        title
        category
        date
        coverCopy,
        cover {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH)
          }
        }
      }
    }
  }
`


/**
 * View
 */
const BlogTemplate:FunctionComponent<IBlogTemplate> = ({ data }) => {
  // format mdx data
  const model = formatPost(data.mdx)
  const wordsCount = data.mdx.wordCount.words || 100

  // cover image
  const coverImage = model.cover && getImage(model.cover)

  // metadata
  const meta:Partial<ISEO> = {
    type: "article",
    category: model.category,
  }

  return (
    <Layout meta={meta}>
      {/* cover */}
      {coverImage && (
        <Cover coverImage={coverImage} copyright={model.coverCopy} />
      )}

      {/* content */}
      <div className="container mt-2 mb-2">
        {/* title */}
        {model.title && (
          <h1 className="h1 h-reading mb-2 mb-sm-3 text-center">
            {model.title}
          </h1>
        )}

        {/* date & duration */}
        <div className="container-reading">
          {(model.date || model.words) && (
            <div className="py-1 my-2 my-sm-4 border-top border-bottom text-muted small">
              <div className="row no-gutters">
                {model.date && (
                  <div className="col-12 col-sm px-1 text-center text-sm-left">
                    publi√© le {model.date}
                  </div>
                )}
                {model.words && (
                  <div className="col-12 col-sm px-1 text-center text-sm-right">
                    {timeToReadMessage(wordsCount)}
                  </div>
                )} 
              </div>
            </div>
          )}

          {/* body */}
          {model.body && (
            <MDXProvider components={mdxComponents}>
              <MDXRenderer>{model.body}</MDXRenderer>
            </MDXProvider>
          )}

          {/* comments */}
          <div className="mt-4 p-1 border rounded shadow-sm">
            <Comments />
          </div>
        </div>
      </div>
    </Layout>
  )
}


export default BlogTemplate